import React, { useState } from "react";
import "./styles.css";

const elemento = <h2>Olá DevPleno do Componente App!</h2>; // JSX
const elemento2 = React.createElement(
  "div",
  null,
  React.createElement("h2", null, "Olá DevPleno do Componente App!")
);

const MostrarI = (props) => {
  return <p>{props.i}</p>;
};

export default function App(props) {
  const [i, setI] = useState(1);
  const increment = () => {
    setI(i + 1);
  };
  return (
    <div className="App">
      <h1>
        Hello {props.name} {i}
      </h1>
      <button onClick={increment}>Incrementar</button>
      <MostrarI i={i} />
      <h2>Start editing to see some magic happen!</h2>
      {elemento}
      {elemento2}
    </div>
  );
}
