import React, { StrictMode } from "react";
import { createRoot } from "react-dom/client";

import App from "./App";
import AppBuscaBinaria from "./AppBuscaBinaria";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

const elemento = (
  <div>
    <h2>Olá DevPleno!</h2>
  </div>
); // jSX
const elemento2 = React.createElement(
  "div",
  null,
  React.createElement("h2", null, "Olá DevPleno!")
);
const elemento3 = React.createElement("h1", null, "Hello World");

root.render(
  <StrictMode>
    <App name="DevPleno" />
    {elemento}
    {elemento2}
    {elemento3}
    <AppBuscaBinaria />
  </StrictMode>
);

